import React, { useEffect, useRef, useMemo, useCallback, useState } from 'react';
import { gsap } from 'gsap';

// Configuration Constants
const FOLLOWER_COUNT = 3;
const SPREAD_FACTOR_FOLLOWERS = 40; 
const DRIFT_SPEED_MIN = 7; 
const DRIFT_SPEED_MAX = 18; 
const DISRUPTION_RADIUS = 100; 
const DISRUPTION_STRENGTH = 70; 
const DISRUPTION_DURATION = 0.4; 
const EDGE_BIAS_PROBABILITY = 0.7; 

const BREAKPOINTS = {
  small: 768, 
  medium: 1280,
  large: 1536 
};
const PARTICLE_COUNTS_CONFIG = {
  small: 50,
  medium: 150,
  large: 250, 
  xlarge: 300 
};


const CELESTIAL_INTERACTION_RADIUS = 200; 
const CELESTIAL_NUDGE_STRENGTH = 25;    
const CELESTIAL_NUDGE_DURATION = 0.4;

type Theme = 'default' | 'space' | 'liquid-glass' | 'sky-blue';

interface AnimatedBackgroundProps {
  theme: Theme;
}

const getResponsiveParticleCount = (): number => {
  const width = window.innerWidth;
  if (width < BREAKPOINTS.small) { 
    return PARTICLE_COUNTS_CONFIG.small;
  } else if (width < BREAKPOINTS.medium) { 
    return PARTICLE_COUNTS_CONFIG.medium;
  } else if (width < BREAKPOINTS.large) { 
    return PARTICLE_COUNTS_CONFIG.large;
  } else { 
    return PARTICLE_COUNTS_CONFIG.xlarge;
  }
};

interface Particle {
  id: number;
  ref: React.RefObject<HTMLDivElement>;
  size: number;
  type: 'follower' | 'drifter';
  initialOpacity: number;
  xTo?: (target: number) => void;
  yTo?: (target: number) => void;
  lag?: number;
  currentTween?: gsap.core.Tween;
  isBeingPushed?: boolean;
  twinkleTween?: gsap.core.Tween;
}

const AnimatedBackground: React.FC<AnimatedBackgroundProps> = ({ theme }) => {
  const containerRef = useRef<HTMLDivElement>(null);
  const sunRef = useRef<HTMLDivElement>(null);
  const moonRef = useRef<HTMLDivElement>(null);
  const mousePosition = useRef({ x: 0, y: 0 });
  const [numTotalParticles, setNumTotalParticles] = useState(getResponsiveParticleCount());

  const getEdgeBiasedRandom = useCallback((maxDimension: number): number => {
    if (Math.random() < 0.5) {
      return Math.random() * Math.random() * maxDimension;
    } else {
      return maxDimension - (Math.random() * Math.random() * maxDimension);
    }
  }, []);

  useEffect(() => {
    const handleResize = () => {
      setNumTotalParticles(getResponsiveParticleCount());
      mousePosition.current = { x: window.innerWidth / 2, y: window.innerHeight / 2 };
    };
    mousePosition.current = { x: window.innerWidth / 2, y: window.innerHeight / 2 };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const particles = useMemo<Particle[]>(() =>
    Array.from({ length: numTotalParticles }).map((_, i) => {
      const type = i < FOLLOWER_COUNT ? 'follower' : 'drifter';
      const isLiquidGlass = theme === 'liquid-glass';
      const isSpace = theme === 'space';
      const isSkyBlue = theme === 'sky-blue';
      
      let size;
      let initialOpacity;

      if (isLiquidGlass) {
        size = Math.random() * 1.5 + 0.8; // Smaller for liquid glass
        initialOpacity = Math.random() * 0.3 + 0.15; // More subtle for liquid glass
      } else if (isSpace) {
        size = Math.random() * 2.5 + 1; // Stars slightly smaller than default
        initialOpacity = Math.random() * 0.6 + 0.4; // Stars more opaque
      } else if (isSkyBlue) {
        size = Math.random() * 2.2 + 0.8; // Slightly smaller and less varied than default, more than liquid
        initialOpacity = Math.random() * 0.5 + 0.2; // Brighter than liquid, softer than space
      }
       else { // Default theme
        size = Math.random() * 3.5 + 1.5;
        initialOpacity = Math.random() * 0.35 + 0.1;
      }
      
      return {
        id: i,
        ref: React.createRef<HTMLDivElement>(),
        size,
        type,
        initialOpacity,
        lag: type === 'follower' ? Math.random() * 0.35 + 0.15 : undefined,
      };
    })
  , [numTotalParticles, theme]);

  const startDrift = useCallback((particle: Particle) => {
    if (!particle.ref.current || particle.type !== 'drifter' || particle.isBeingPushed) return;
    if (particle.currentTween) particle.currentTween.kill();

    const { innerWidth, innerHeight } = window;
    let destCenterX: number, destCenterY: number;

    if (Math.random() < EDGE_BIAS_PROBABILITY) {
        destCenterX = getEdgeBiasedRandom(innerWidth);
        destCenterY = getEdgeBiasedRandom(innerHeight);
    } else {
        destCenterX = Math.random() * innerWidth;
        destCenterY = Math.random() * innerHeight;
    }

    particle.currentTween = gsap.to(particle.ref.current, {
      x: destCenterX - particle.size / 2,
      y: destCenterY - particle.size / 2,
      duration: Math.random() * (DRIFT_SPEED_MAX - DRIFT_SPEED_MIN) + DRIFT_SPEED_MIN,
      ease: "sine.inOut",
      onComplete: () => { if (particle.ref.current) startDrift(particle); },
    });
  }, [getEdgeBiasedRandom]);

  useEffect(() => {
    if (sunRef.current && moonRef.current) {
      if (theme === 'liquid-glass') {
        gsap.to(sunRef.current, { opacity: 0, duration: 0.7, ease: 'power2.inOut' });
        gsap.to(moonRef.current, { opacity: 0, duration: 0.7, ease: 'power2.inOut' });
      } else if (theme === 'space') {
        gsap.to(sunRef.current, { opacity: 0, y: "+=50px", duration: 1, ease: 'power2.inOut' });
        gsap.to(moonRef.current, { opacity: 1, y: "0px", duration: 1, ease: 'power2.inOut', delay: 0.3 });
      } else if (theme === 'sky-blue') {
        gsap.to(sunRef.current, { opacity: 1, y: "0px", duration: 1, ease: 'power2.inOut', delay: 0.3 }); // Sun visible
        gsap.to(moonRef.current, { opacity: 0, y: "+=50px", duration: 1, ease: 'power2.inOut' }); // Moon hidden
      }
       else { // Default theme
        gsap.to(sunRef.current, { opacity: 1, y: "0px", duration: 1, ease: 'power2.inOut', delay: 0.3 });
        gsap.to(moonRef.current, { opacity: 0, y: "+=50px", duration: 1, ease: 'power2.inOut' });
      }
    }
  }, [theme]);
  
  useEffect(() => {
    if (mousePosition.current.x === 0 && mousePosition.current.y === 0 && window.innerWidth > 0) {
        mousePosition.current = { x: window.innerWidth / 2, y: window.innerHeight / 2 };
    }

    const ctx = gsap.context(() => {
      const viewportWidth = window.innerWidth;
      const viewportHeight = window.innerHeight;

      particles.forEach(particle => {
        if (!particle.ref.current) return;
        const pElement = particle.ref.current;
        gsap.killTweensOf(pElement); 
        if (particle.twinkleTween) particle.twinkleTween.kill();

        gsap.set(pElement, {
          x: Math.random() * viewportWidth - particle.size / 2,
          y: Math.random() * viewportHeight - particle.size / 2,
          width: particle.size,
          height: particle.size,
          opacity: particle.initialOpacity,
          backgroundColor: 'var(--current-particle-bg)', 
        });
        
        if (theme === 'space' && particle.type === 'drifter' && Math.random() < 0.3) { 
            particle.twinkleTween = gsap.to(pElement, {
                opacity: () => particle.initialOpacity * (Math.random() * 0.4 + 0.3), 
                duration: Math.random() * 2 + 1.5,
                repeat: -1,
                yoyo: true,
                ease: "sine.inOut",
                delay: Math.random() * 2,
            });
        } else { // For default, liquid-glass, and sky-blue ensure no twinkle
            if (particle.twinkleTween) {
                particle.twinkleTween.kill(); // Ensure killed
                gsap.set(pElement, {opacity: particle.initialOpacity}); // Reset opacity
            }
        }

        if (particle.type === 'follower' && particle.lag) {
          particle.xTo = gsap.quickTo(pElement, "x", { duration: particle.lag, ease: "power2.out" });
          particle.yTo = gsap.quickTo(pElement, "y", { duration: particle.lag, ease: "power2.out" });
          const spreadFactorBase = theme === 'liquid-glass' ? 1.5 : 
                                   (theme === 'space' ? 2.5 : 
                                   (theme === 'sky-blue' ? 2.0 : 3.5));
          const initialOffsetX = (Math.random() - 0.5) * SPREAD_FACTOR_FOLLOWERS * (particle.size / spreadFactorBase);
          const initialOffsetY = (Math.random() - 0.5) * SPREAD_FACTOR_FOLLOWERS * (particle.size / spreadFactorBase);
          particle.xTo(mousePosition.current.x + initialOffsetX - particle.size / 2);
          particle.yTo(mousePosition.current.y + initialOffsetY - particle.size / 2);
        } else if (particle.type === 'drifter') {
          startDrift(particle);
        }
      });

      const handleCelestialInteraction = (
        element: HTMLDivElement | null,
        interactionRadius: number,
        nudgeStrength: number,
        nudgeDuration: number
      ) => {
        if (!element || theme === 'liquid-glass') return; // No celestial interaction in liquid-glass

        const rect = element.getBoundingClientRect();
        const centerX = rect.left + rect.width / 2;
        const centerY = rect.top + rect.height / 2;

        const dxMouse = centerX - mousePosition.current.x;
        const dyMouse = centerY - mousePosition.current.y;
        const distanceMouse = Math.sqrt(dxMouse * dxMouse + dyMouse * dyMouse);

        if (distanceMouse < interactionRadius) {
          const angle = Math.atan2(dyMouse, dxMouse);
          const nudgeX = Math.cos(angle) * nudgeStrength;
          const nudgeY = Math.sin(angle) * nudgeStrength;

          gsap.to(element, {
            x: nudgeX,
            y: nudgeY,
            duration: nudgeDuration,
            ease: "power2.out"
          });
        } else {
          gsap.to(element, {
            x: 0,
            y: 0,
            duration: nudgeDuration,
            ease: "power2.out"
          });
        }
      };

      const handleMouseMove = (event: MouseEvent) => {
        mousePosition.current = { x: event.clientX, y: event.clientY };

        if (theme !== 'liquid-glass') { // Only interact with celestial bodies if not liquid-glass
            handleCelestialInteraction(sunRef.current, CELESTIAL_INTERACTION_RADIUS, CELESTIAL_NUDGE_STRENGTH, CELESTIAL_NUDGE_DURATION);
            handleCelestialInteraction(moonRef.current, CELESTIAL_INTERACTION_RADIUS, CELESTIAL_NUDGE_STRENGTH, CELESTIAL_NUDGE_DURATION);
        }
        
        particles.forEach(particle => {
          if (!particle.ref.current) return;
          if (particle.type === 'follower' && particle.xTo && particle.yTo) {
            const spreadFactorBase = theme === 'liquid-glass' ? 1.5 : 
                                     (theme === 'space' ? 2.5 : 
                                     (theme === 'sky-blue' ? 2.0 : 3.5));
            const spreadInfluence = particle.size / spreadFactorBase;
            const offsetX = (Math.random() - 0.5) * SPREAD_FACTOR_FOLLOWERS * spreadInfluence;
            const offsetY = (Math.random() - 0.5) * SPREAD_FACTOR_FOLLOWERS * spreadInfluence;
            particle.xTo(mousePosition.current.x + offsetX - particle.size / 2);
            particle.yTo(mousePosition.current.y + offsetY - particle.size / 2);
          } else if (particle.type === 'drifter' && !particle.isBeingPushed) {
            const pElement = particle.ref.current;
            const currentX = gsap.getProperty(pElement, "x") as number + particle.size / 2;
            const currentY = gsap.getProperty(pElement, "y") as number + particle.size / 2;
            const dx = currentX - mousePosition.current.x;
            const dy = currentY - mousePosition.current.y;
            const distance = Math.sqrt(dx * dx + dy * dy);

            if (distance < DISRUPTION_RADIUS) {
              particle.isBeingPushed = true;
              if (particle.currentTween) particle.currentTween.kill();
              if (particle.twinkleTween && theme === 'space') particle.twinkleTween.pause(); // Only pause twinkle if it exists (space theme)

              const angle = Math.atan2(dy, dx);
              const targetX = (currentX + Math.cos(angle) * DISRUPTION_STRENGTH) - particle.size / 2;
              const targetY = (currentY + Math.sin(angle) * DISRUPTION_STRENGTH) - particle.size / 2;
              
              gsap.to(pElement, {
                x: targetX, y: targetY, duration: DISRUPTION_DURATION, ease: "power1.out",
                onComplete: () => {
                  particle.isBeingPushed = false;
                  if (particle.twinkleTween && theme === 'space') particle.twinkleTween.play();
                  if (particle.ref.current) startDrift(particle);
                }
              });
            }
          }
        });
      };

      window.addEventListener('mousemove', handleMouseMove);
      return () => {
        window.removeEventListener('mousemove', handleMouseMove);
        particles.forEach(p => {
            if(p.currentTween) p.currentTween.kill();
            if(p.twinkleTween) p.twinkleTween.kill();
            if(p.ref.current) gsap.killTweensOf(p.ref.current);
        });
        if (sunRef.current) gsap.killTweensOf(sunRef.current);
        if (moonRef.current) gsap.killTweensOf(moonRef.current);
      };
    }, containerRef); 

    return () => ctx.revert();
  }, [particles, startDrift, theme]);

  return (
    <div
      ref={containerRef}
      className="fixed inset-0 pointer-events-none z-0 overflow-hidden"
      aria-hidden="true"
    >
      <div 
        ref={sunRef}
        className="absolute 
                   top-[5%] left-[5%] 
                   w-[120px] h-[120px] sm:w-[180px] sm:h-[180px] md:w-[220px] md:h-[220px] lg:w-[260px] lg:h-[260px] xl:w-[300px] xl:h-[300px] 2xl:w-[340px] 2xl:h-[340px] 
                   max-w-[90vw] max-h-[40vw] 
                   rounded-full opacity-0" // Initial opacity for GSAP
        style={{
          background: 'radial-gradient(circle, var(--current-sun-color) 0%, rgba(245,211,0,0.5) 40%, rgba(245,211,0,0) 70%)',
          filter: 'blur(30px)',
        }}
      />
      <div 
        ref={moonRef}
        className="absolute 
                   bottom-[12%] right-[8%] xl:bottom-[15%] xl:right-[12%] 2xl:bottom-[18%] 2xl:right-[15%]
                   w-[70px] h-[70px] sm:w-[90px] sm:h-[90px] md:w-[120px] md:h-[120px] 
                   lg:w-[150px] lg:h-[150px] xl:w-[170px] xl:h-[170px] 2xl:w-[190px] 2xl:h-[190px] 
                   rounded-full opacity-0" // Initial opacity for GSAP
        style={{
          background: 'radial-gradient(circle, var(--current-moon-color) 0%, rgba(224,231,255,0.4) 50%, rgba(224,231,255,0) 70%)',
          filter: 'blur(20px)',
          transform: 'translateY(50px)' // Initial offset for GSAP animation
        }}
      />
      {particles.map(p => (
        <div
          key={p.id}
          ref={p.ref}
          className="absolute rounded-full" 
          style={{ backgroundColor: 'var(--current-particle-bg)' }} // Dynamic color via CSS var
        />
      ))}
    </div>
  );
};

export default AnimatedBackground;