
import React, { useRef } from 'react';
import { gsap } from 'gsap';
import { Project } from '../constants'; 

interface ProjectCardProps {
  project: Project;
}

const ProjectCard: React.FC<ProjectCardProps> = ({ project }) => {
  const cardRef = useRef<HTMLDivElement>(null);
  const linkRef = useRef<HTMLAnchorElement>(null);
  const emojiRef = useRef<HTMLSpanElement>(null);
  const titleExperimentRef = useRef<HTMLHeadingElement>(null); // Ref for the experiment title

  const handleCardMouseEnter = () => {
    if (cardRef.current) {
      gsap.to(cardRef.current, { 
        scale: 1.04, // Slightly more pronounced scale
        boxShadow: "0 12px 25px -5px var(--current-card-shadow-color), 0 8px 10px -6px var(--current-card-shadow-color)", // Use new var
        borderColor: "var(--current-project-card-border-hover)",
        duration: 0.25, 
        ease: 'power2.out'
      });
    }
    if (titleExperimentRef.current) {
      gsap.to(titleExperimentRef.current, { 
        y: -2, 
        color: 'var(--current-brand-text)', // Ensure it uses the main text color
        textShadow: '0 0 6px var(--current-brand-text-glow-strong)',
        duration: 0.2, 
        ease: 'power1.out' 
      });
    }
  };

  const handleCardMouseLeave = () => {
    if (cardRef.current) {
      gsap.to(cardRef.current, { 
        scale: 1, 
        boxShadow: '0 10px 15px -3px rgba(0,0,0,0.1), 0 4px 6px -2px rgba(0,0,0,0.05)', // Default Tailwind lg shadow
        borderColor: "var(--current-project-card-border)",
        duration: 0.2, 
        ease: 'power2.out'
      });
    }
    if (titleExperimentRef.current) {
      gsap.to(titleExperimentRef.current, { 
        y: 0, 
        color: 'var(--current-brand-text)', // Revert to base color if it changed
        textShadow: '0 0 5px var(--current-brand-text-glow)', // Revert to base glow
        duration: 0.2, 
        ease: 'power1.out' 
      });
    }
  };


  const handleLinkMouseEnter = () => {
    if (emojiRef.current) {
      gsap.to(emojiRef.current, { 
        rotate: 360, 
        scale: 1.25, // Slightly larger
        duration: 0.45, 
        ease: 'elastic.out(1.1, 0.45)' // Adjusted elasticity
      });
    }
    if (linkRef.current) {
        gsap.to(linkRef.current, { color: 'var(--current-brand-text)', textShadow: '0 0 8px var(--current-brand-text-glow-strong)', duration: 0.2});
    }
  };

  const handleLinkMouseLeave = () => {
    if (emojiRef.current) {
      gsap.to(emojiRef.current, { 
        rotate: 0, 
        scale: 1,
        duration: 0.3, 
        ease: 'power2.out' 
      });
    }
    if (linkRef.current) {
        gsap.to(linkRef.current, { color: 'var(--current-brand-text)', textShadow: 'none', duration: 0.2});
    }
  };

  return (
    <div 
      ref={cardRef}
      className="bg-[var(--current-project-card-bg)] rounded-lg border border-[var(--current-project-card-border)] p-6 md:p-8 shadow-lg 
                 transition-colors duration-[var(--global-transition-duration)] ease-in-out /* For theme changes */
                 flex flex-col h-full cursor-pointer" // Added cursor-pointer as whole card is interactive
      role="article"
      aria-labelledby={`project-title-${project.id}`}
      onMouseEnter={handleCardMouseEnter}
      onMouseLeave={handleCardMouseLeave}
      onFocus={handleCardMouseEnter} // Mirror hover for keyboard users
      onBlur={handleCardMouseLeave}
      tabIndex={0} // Make div focusable
      style={{ willChange: 'transform, box-shadow, border-color' }} // Perf hint
    >
      <div className="mb-4">
        <h3 
          id={`project-title-${project.id}`}
          ref={titleExperimentRef}
          className="text-[var(--current-brand-text)] font-semibold text-lg md:text-xl text-glow"
          style={{ willChange: 'transform, color, text-shadow' }} // Perf hint
        >
          <span role="img" aria-label="Experiment Flask Emoji" className="mr-2">🧪</span>experiment #{project.experimentNumber}
        </h3>
      </div>
      
      <div className="flex-grow mb-5">
        <p className="text-gray-100 text-base md:text-lg mb-2 leading-relaxed">
          {project.title}
        </p>
        <p className="text-gray-400 text-sm md:text-base leading-relaxed">
          {project.subtitle}
        </p>
      </div>
      
      <div>
        <a 
          ref={linkRef}
          href={project.linkUrl} 
          target="_blank" 
          rel="noopener noreferrer"
          className="inline-flex items-center text-[var(--current-brand-text)] font-medium text-sm md:text-base hover:underline focus-visible:underline focus:outline-none focus-visible:ring-1 focus-visible:ring-[var(--current-brand-text)] focus-visible:ring-opacity-50 rounded group"
          aria-label={`${project.linkText} for experiment ${project.experimentNumber}`}
          onMouseEnter={handleLinkMouseEnter}
          onMouseLeave={handleLinkMouseLeave}
          onFocus={handleLinkMouseEnter} 
          onBlur={handleLinkMouseLeave}   
          style={{ willChange: 'color, text-shadow' }} // Perf hint
        >
          <span ref={emojiRef} role="img" aria-label="Link Emoji" className="mr-1.5 inline-block" style={{ willChange: 'transform' }}>🔗</span>{project.linkText}
        </a>
      </div>
    </div>
  );
};

export default ProjectCard;
