
import React, { useEffect, useRef, forwardRef } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { ExperienceItem } from '../constants';

gsap.registerPlugin(ScrollTrigger);

interface ExperienceSectionProps {
  experiences: ExperienceItem[];
}

const ExperienceCard: React.FC<{ item: ExperienceItem }> = ({ item }) => {
  const cardRef = useRef<HTMLDivElement>(null);
  const companyLinkRef = useRef<HTMLAnchorElement>(null);
  const titleRef = useRef<HTMLHeadingElement>(null); // Added ref for the title

  const handleMouseEnter = () => {
    if (cardRef.current) {
      gsap.to(cardRef.current, { 
        y: -8, 
        scale: 1.015, 
        boxShadow: "0 12px 22px -4px var(--current-card-shadow-color), 0 8px 10px -6px var(--current-card-shadow-color)",
        borderColor: "var(--current-project-card-border-hover)",
        duration: 0.3, 
        ease: 'power2.out' 
      });
    }
    if (titleRef.current) { // Animate title's text shadow on card hover
      gsap.to(titleRef.current, {
        textShadow: '0 0 10px var(--current-brand-text-glow-strong), 0 0 18px var(--current-brand-text-glow-strong)',
        duration: 0.25,
        ease: 'power1.out'
      });
    }
  };

  const handleMouseLeave = () => {
    if (cardRef.current) {
      gsap.to(cardRef.current, { 
        y: 0, 
        scale: 1, 
        boxShadow: '0 10px 15px -3px rgba(0,0,0,0.1), 0 4px 6px -2px rgba(0,0,0,0.05)', 
        borderColor: "var(--current-project-card-border)",
        duration: 0.25, 
        ease: 'power2.out' 
      });
    }
    if (titleRef.current) { // Revert title's text shadow
      gsap.to(titleRef.current, {
        textShadow: '0 0 8px var(--current-brand-text-glow), 0 0 12px var(--current-brand-text-glow)', // From .text-glow class
        duration: 0.3,
        ease: 'power1.out'
      });
    }
  };

  const handleCompanyLinkMouseEnter = () => {
    if (companyLinkRef.current) {
      gsap.to(companyLinkRef.current, {
        y: -2,
        backgroundColor: 'var(--current-brand-text)', 
        color: 'var(--current-brand-bg)', 
        boxShadow: '0 4px 8px rgba(0,0,0,0.1)',
        duration: 0.2,
        ease: 'power1.out'
      });
    }
  };

  const handleCompanyLinkMouseLeave = () => {
    if (companyLinkRef.current) {
      gsap.to(companyLinkRef.current, {
        y: 0,
        backgroundColor: 'rgb(229 231 235)', 
        color: 'rgb(17 24 39)', 
        boxShadow: 'none',
        duration: 0.2,
        ease: 'power1.out'
      });
    }
  };


  return (
    <div 
      ref={cardRef}
      className="bg-nav-bg bg-opacity-[0.9] rounded-lg border border-[var(--current-project-card-border)] shadow-lg 
                 transition-colors duration-[var(--global-transition-duration)] /* For theme changes of bg/border */
                 overflow-hidden" 
      role="article"
      aria-labelledby={`experience-title-${item.id}`}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      onFocus={handleMouseEnter} 
      onBlur={handleMouseLeave}
      tabIndex={0} 
      style={{ willChange: 'transform, box-shadow, border-color' }} 
    >
      <div className="px-6 md:px-8 pt-6 md:pt-8 pb-2">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-1">
          <div className="flex items-center mb-2 sm:mb-0">
            <a 
              ref={companyLinkRef}
              href={item.website || '#'} 
              target="_blank" 
              rel="noopener noreferrer"
              className="bg-gray-200 text-gray-900 text-sm sm:text-base font-sans font-semibold py-1 px-3 sm:py-1.5 sm:px-4 rounded-sm transition-colors duration-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-[var(--current-brand-text)]"
              aria-label={`visit ${item.company} website`}
              onMouseEnter={handleCompanyLinkMouseEnter}
              onMouseLeave={handleCompanyLinkMouseLeave}
              onFocus={handleCompanyLinkMouseEnter}
              onBlur={handleCompanyLinkMouseLeave}
              style={{ willChange: 'transform, background-color, color, box-shadow' }} 
            >
              {item.company}
            </a>
            <span className="text-gray-400 text-xs sm:text-sm font-sans ml-3 sm:ml-4">{item.role}</span>
          </div>
          <span className="text-gray-500 text-xs sm:text-sm font-mono whitespace-nowrap">{item.duration}</span>
        </div>
      </div>

      {item.acquisitionNote && (
        <div className="bg-[var(--current-experience-banner-bg)] text-[var(--current-experience-banner-text)] text-xs font-sans py-1.5 px-6 md:px-8 transition-colors duration-[var(--global-transition-duration)]">
          <div className="flex space-x-4 overflow-hidden">
            <span className="marquee-text">* {item.acquisitionNote}</span>
            <span className="marquee-text hidden sm:inline">* {item.acquisitionNote}</span>
            <span className="marquee-text hidden md:inline">* {item.acquisitionNote}</span>
            {/* Add more for seamless effect if needed, or use JS for true marquee */}
          </div>
        </div>
      )}

      <div className="px-6 md:px-8 pt-3 pb-6 md:pb-8">
        <h3 
          id={`experience-title-${item.id}`}
          ref={titleRef} // Applied ref
          className="text-[var(--current-brand-text)] font-sans font-semibold text-xl md:text-2xl text-glow mb-3"
          style={{ willChange: 'text-shadow' }} // Performance hint for text-shadow
        >
          {item.mainTitle}
        </h3>
        <p className="text-gray-300 text-sm md:text-base font-sans leading-relaxed mb-6 hidden md:block">
          {item.description}
        </p>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-x-6 gap-y-4 md:gap-y-0">
          <div>
            <h4 className="text-gray-500 text-xs font-mono uppercase tracking-wider mb-2">disciplines</h4>
            <ul className="list-disc list-inside space-y-1">
              {item.disciplines.map(disc => (
                <li key={disc} className="text-gray-300 text-sm font-sans">{disc}</li>
              ))}
            </ul>
          </div>
          <div>
            <h4 className="text-gray-500 text-xs font-mono uppercase tracking-wider mb-2">my role</h4>
            <p className="text-gray-300 text-sm font-sans leading-relaxed">
              {item.myRoleDetails}
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};


const ExperienceSection = forwardRef<HTMLDivElement, ExperienceSectionProps>(({ experiences }, ref) => {
  const sectionRef = (ref as React.RefObject<HTMLDivElement>) || useRef<HTMLDivElement>(null);
  const cardsWrapperRef = useRef<(HTMLDivElement | null)[]>([]); 

  useEffect(() => {
    cardsWrapperRef.current = cardsWrapperRef.current.slice(0, experiences.length);
  }, [experiences]);

  useEffect(() => {
    const ctx = gsap.context(() => {
      if (cardsWrapperRef.current.length > 0 && sectionRef.current) {
        cardsWrapperRef.current.forEach((cardWrapper, index) => {
          if (cardWrapper) {
            gsap.fromTo(cardWrapper, 
              { opacity: 0, y: 50, scale: 0.98 },
              {
                opacity: 1,
                y: 0,
                scale: 1,
                duration: 0.65, 
                ease: 'power3.out',
                scrollTrigger: {
                  trigger: cardWrapper,
                  start: 'top 88%', 
                  toggleActions: 'play none none none',
                },
                delay: index * 0.12, 
              }
            );
          }
        });
      }
    }, sectionRef);

    return () => {
      ctx.revert();
    };
  }, [experiences, sectionRef]);

  return (
    <section 
      id="journey" 
      ref={sectionRef} 
      className="w-full pt-0 pb-16 md:pb-24" 
    >
      <div className="container mx-auto px-6 max-w-5xl"> 
        {experiences.length > 0 ? (
          <div className="space-y-10 md:space-y-12">
            {experiences.map((item, index) => (
              <div key={item.id} ref={el => { cardsWrapperRef.current[index] = el; }} style={{ willChange: 'opacity, transform' }}>
                <ExperienceCard item={item} />
              </div>
            ))}
          </div>
        ) : (
          <p className="text-center text-gray-400">details about my professional journey coming soon...</p>
        )}
      </div>
    </section>
  );
});

ExperienceSection.displayName = 'ExperienceSection';
export default ExperienceSection;
