import React, { useEffect, useRef, useState } from 'react';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import Navbar from './components/Navbar';
// import Footer from './components/Footer'; // Removed Footer
import AnimatedBackground from './components/AnimatedBackground';
import useTypewriter from './hooks/useTypewriter';
import { NAV_LINKS, MAIN_CONTENT, PROJECTS_DATA, SNAPS_DATA, LAB_NOTES_DATA, EXPERIENCE_DATA } from './constants';
import ProjectsSection from './components/ProjectsSection';
import SnapsSection from './components/SnapsSection';
import FloatingWorkButton from './components/FloatingWorkButton';
import LabNotesSection from './components/LabNotesSection'; 
import ExperienceSection from './components/ExperienceSection'; 
import ContactCard from './components/ContactCard'; // Added ContactCard

gsap.registerPlugin(ScrollTrigger);

type Theme = 'default' | 'space' | 'liquid-glass' | 'sky-blue';

const App: React.FC = () => {
  const appContainerRef = useRef<HTMLDivElement>(null);
  const greetingRef = useRef<HTMLParagraphElement>(null);
  const introRef = useRef<HTMLParagraphElement>(null);
  const philosophyRef = useRef<HTMLParagraphElement>(null);
  
  const labNotesRef = useRef<HTMLDivElement>(null); 
  const experienceSectionRef = useRef<HTMLDivElement>(null); 

  const projectsSectionRef = useRef<HTMLDivElement>(null);
  const projectsTitleRef = useRef<HTMLHeadingElement>(null);
  const projectsDescriptionRef = useRef<HTMLParagraphElement>(null);
  
  const snapsSectionRef = useRef<HTMLDivElement>(null); 
  const snapsTitleRef = useRef<HTMLHeadingElement>(null);
  const snapsDescriptionRef = useRef<HTMLParagraphElement>(null);

  const contactCardSectionRef = useRef<HTMLDivElement>(null); // Ref for ContactCard

  const floatingButtonRef = useRef<HTMLDivElement>(null);

  const [currentTheme, setCurrentTheme] = useState<Theme>('default');

  const [typedGreeting, , isGreetingDone] = useTypewriter(MAIN_CONTENT.greeting, {
    loop: false,
    typeSpeed: 30,
    deleteSpeed: 50,
    delaySpeed: 1000,
  });

  useEffect(() => {
    document.body.classList.remove('theme-default', 'theme-space', 'theme-liquid-glass', 'theme-sky-blue');
    document.body.classList.add(`theme-${currentTheme}`);
    
    // Trigger for CSS transitions relying on these variables
    gsap.to(document.body, { 
        duration: 0, // No actual animation, just a trigger
        '--theme-transition-trigger': Math.random() // Force style re-evaluation
    });
  }, [currentTheme]);


  useEffect(() => {
    const ctx = gsap.context(() => {
      gsap.set(
        [
          introRef.current, 
          philosophyRef.current, 
          labNotesRef.current, 
          projectsDescriptionRef.current,
          snapsDescriptionRef.current,
          contactCardSectionRef.current 
        ].filter(Boolean), 
        { opacity: 0, y: 20 }
      );

      if (floatingButtonRef.current) {
        gsap.set(floatingButtonRef.current, { opacity: 0, y: 50 });
      }

      gsap.fromTo(
        greetingRef.current, 
        { opacity: 0, y: 20 }, 
        { opacity: 1, y: 0, duration: 0.6, ease: 'power3.out', delay: 0.2 }
      );

      if (labNotesRef.current) {
        gsap.fromTo(labNotesRef.current,
          { opacity: 0, y: 30 },
          {
            opacity: 1, y: 0, duration: 0.8, ease: 'power3.out',
            scrollTrigger: {
              trigger: labNotesRef.current,
              start: 'top 85%', 
              toggleActions: 'play none none none',
            }
          }
        );
      }
      
      if (projectsTitleRef.current && projectsSectionRef.current) {
        gsap.fromTo(projectsTitleRef.current,
          { opacity: 0, y: 30 },
          {
            opacity: 1, y: 0, duration: 0.8, ease: 'power3.out',
            scrollTrigger: {
              trigger: projectsSectionRef.current,
              start: 'top 80%', 
              toggleActions: 'play none none none',
            }
          }
        );
      }

      if (projectsDescriptionRef.current && projectsSectionRef.current) {
        gsap.fromTo(projectsDescriptionRef.current,
          { opacity: 0, y: 25 },
          {
            opacity: 1, y: 0, duration: 0.7, ease: 'power3.out',
            scrollTrigger: {
              trigger: projectsSectionRef.current,
              start: 'top 78%',
              toggleActions: 'play none none none',
            },
            delay: 0.15
          }
        );
      }

      if (snapsTitleRef.current && snapsSectionRef.current) {
        gsap.fromTo(snapsTitleRef.current,
          { opacity: 0, y: 30 },
          {
            opacity: 1, y: 0, duration: 0.8, ease: 'power3.out',
            scrollTrigger: {
              trigger: snapsSectionRef.current,
              start: 'top 80%', 
              toggleActions: 'play none none none',
            }
          }
        );
      }
      
      if (snapsDescriptionRef.current && snapsSectionRef.current) {
        gsap.fromTo(snapsDescriptionRef.current,
          { opacity: 0, y: 25 },
          {
            opacity: 1, y: 0, duration: 0.7, ease: 'power3.out',
            scrollTrigger: {
              trigger: snapsSectionRef.current, 
              start: 'top 78%', 
              toggleActions: 'play none none none',
            },
            delay: 0.15 
          }
        );
      }

      if (contactCardSectionRef.current) { 
        gsap.fromTo(contactCardSectionRef.current,
          { opacity: 0, y: 50 },
          {
            opacity: 1, y: 0, duration: 0.8, ease: 'power3.out',
            scrollTrigger: {
              trigger: contactCardSectionRef.current,
              start: 'top 85%', 
              toggleActions: 'play none none none',
            }
          }
        );
      }
      
      // Theme ScrollTriggers
      if (projectsSectionRef.current) {
          ScrollTrigger.create({
              trigger: projectsSectionRef.current,
              start: "top center",
              end: "bottom center", 
              id: "projects-theme",
              onEnter: () => setCurrentTheme('space'),
              onLeaveBack: () => setCurrentTheme('default'),
              onLeave: () => setCurrentTheme('space'), 
              onEnterBack: () => setCurrentTheme('space'),
          });
      }

      if (snapsSectionRef.current) {
          ScrollTrigger.create({
              trigger: snapsSectionRef.current,
              start: "top center",
              end: "bottom center",
              id: "snaps-theme",
              onEnter: () => setCurrentTheme('sky-blue'), 
              onLeaveBack: () => setCurrentTheme('space'),
              onLeave: () => setCurrentTheme('sky-blue'), 
              onEnterBack: () => setCurrentTheme('sky-blue'),
          });
      }
      
      if (contactCardSectionRef.current) {
        ScrollTrigger.create({
            trigger: contactCardSectionRef.current,
            start: "top center",
            end: "bottom center", // Ensure this covers the whole section
            id: "contact-card-theme",
            onEnter: () => setCurrentTheme('default'),
            onLeaveBack: () => setCurrentTheme('sky-blue'),
            // Optional: onLeave and onEnterBack if you want different behavior when scrolling past
            // onLeave: () => setCurrentTheme('default'), 
            // onEnterBack: () => setCurrentTheme('default'),
        });
      }

      // Hero text hover microinteraction
      [greetingRef.current, introRef.current, philosophyRef.current].filter(Boolean).forEach(el => {
        if (el) {
          el.addEventListener('mouseenter', () => {
            gsap.to(el, { 
              textShadow: "0 0 12px var(--current-brand-text-glow-strong), 0 0 20px var(--current-brand-text-glow-strong)", 
              duration: 0.25, 
              ease: 'power2.out' 
            });
          });
          el.addEventListener('mouseleave', () => {
            gsap.to(el, { 
              textShadow: "0 0 8px var(--current-brand-text-glow), 0 0 12px var(--current-brand-text-glow)", 
              duration: 0.3, 
              ease: 'power2.out' 
            });
          });
        }
      });
      
    }, appContainerRef);

    return () => {
      ctx.revert();
      ScrollTrigger.getAll().forEach(trigger => trigger.kill());
      [greetingRef.current, introRef.current, philosophyRef.current].filter(Boolean).forEach(el => {
        gsap.killTweensOf(el);
        if (el && el.parentNode) {
            // A common way to remove all listeners if you don't have direct references
            const newEl = el.cloneNode(true);
            el.parentNode.replaceChild(newEl, el);
            // Update refs if necessary, though for these specific elements, they are re-queried on next effect or stable.
            // For example, if greetingRef was used elsewhere, it would point to the old, detached element.
            // However, in this component's lifecycle, this cleanup is likely okay before unmount.
        }
      });
    };
  }, []); 

  useEffect(() => {
    let ctx: gsap.Context | undefined;
    if (isGreetingDone) {
      if (introRef.current && philosophyRef.current && floatingButtonRef.current) {
        ctx = gsap.context(() => {
          const tl = gsap.timeline();
          tl.to(introRef.current!, { 
              opacity: 1, 
              y: 0, 
              duration: 0.6, 
              ease: 'power3.out',
              delay: 0.1 
            })
            .to(philosophyRef.current!, { 
              opacity: 1, 
              y: 0, 
              duration: 0.6, 
              ease: 'power3.out' 
            }, "-=0.4")
            .to(floatingButtonRef.current!, { 
              opacity: 1,
              y: 0,
              duration: 0.6,
              ease: 'power3.out'
            }, "-=0.4"); 
        }, appContainerRef);
      }
    }
    return () => {
      if (ctx) {
        ctx.revert();
      }
    };
  }, [isGreetingDone, appContainerRef]);


  return (
    <div 
      ref={appContainerRef} 
      className="bg-[var(--current-brand-bg)] text-[var(--current-brand-text)] min-h-screen flex flex-col items-center selection:bg-[var(--selection-bg)] selection:text-[var(--selection-text)] overflow-x-hidden"
      role="application"
      aria-label="Owais Khan's Portfolio - Internet Lab"
    >
      <AnimatedBackground theme={currentTheme} />
      <div className="relative z-10 flex flex-col items-center w-full min-h-screen">
        <Navbar navLinks={NAV_LINKS} brandName={MAIN_CONTENT.name} />

        <main 
          className="container mx-auto flex-grow flex flex-col items-center 
                     max-w-4xl xl:max-w-5xl 2xl:max-w-6xl px-6 
                     pt-24 
                     sm:pt-28 md:pt-32
                     lg:pt-40
                     xl:pt-48
                     2xl:pt-56
                     pb-8 md:pb-10 lg:pb-12 xl:pb-14 2xl:pb-16
                     space-y-7"
          role="main"
          aria-label="Main content - Introduction and overview"
        >
          {/* Hero Section */}
          <section 
            className="text-center space-y-7"
            aria-labelledby="greeting-heading"
            role="banner"
          >
            <h1 
              id="greeting-heading"
              ref={greetingRef} 
              className="text-lg sm:text-xl md:text-2xl xl:text-3xl font-medium text-center text-glow"
              aria-live="polite"
              aria-label="Welcome greeting"
            >
              {typedGreeting}
              {!isGreetingDone && <span className="opacity-75 animate-pulse" aria-hidden="true">_</span>}
            </h1>
            <p 
              ref={introRef} 
              className="text-base sm:text-lg md:text-xl xl:text-2xl leading-relaxed opacity-0 text-center text-glow"
              aria-label="Professional introduction"
            >
              {MAIN_CONTENT.introduction}
            </p>
            <p 
              ref={philosophyRef} 
              className="text-base sm:text-lg md:text-xl xl:text-2xl leading-relaxed opacity-0 text-center text-glow"
              aria-label="Personal philosophy and approach"
            >
              {MAIN_CONTENT.philosophy}
            </p>
          </section>
        </main>
        
        {/* Lab Notes Section */}
        <section 
          aria-labelledby="lab-notes-heading"
          role="region"
          aria-label="Lab notes and insights"
        >
          <LabNotesSection ref={labNotesRef} notes={LAB_NOTES_DATA} />
        </section>

        {/* Experience Section */}
        <section 
          aria-labelledby="experience-heading"
          role="region"
          aria-label="Professional experience and achievements"
        >
          <ExperienceSection
            experiences={EXPERIENCE_DATA}
            ref={experienceSectionRef}
          />
        </section>

        {/* Projects Section */}
        <section 
          aria-labelledby="projects-heading"
          role="region"
          aria-label="Software projects and experiments"
        >
          <ProjectsSection 
              projects={PROJECTS_DATA} 
              ref={projectsSectionRef} 
              titleRef={projectsTitleRef} 
              descriptionRef={projectsDescriptionRef}
          />
        </section>

        {/* Snaps Section */}
        <section 
          aria-labelledby="snaps-heading"
          role="region"
          aria-label="Visual snapshots and moments"
        >
          <SnapsSection 
              snaps={SNAPS_DATA}
              ref={snapsSectionRef}
              titleRef={snapsTitleRef}
              descriptionRef={snapsDescriptionRef}
          />
        </section>

        {/* Contact Section */}
        <section 
          aria-labelledby="contact-heading"
          role="region"
          aria-label="Contact information and collaboration opportunities"
        >
          <ContactCard ref={contactCardSectionRef} /> 
        </section>
        
        {/* Floating Action Button */}
        <FloatingWorkButton ref={floatingButtonRef} />

        {/* <Footer /> Removed Footer */}
      </div>
    </div>
  );
};

export default App;
