import { useState, useEffect, useCallback, useRef } from 'react';

interface TypewriterOptions {
  loop?: boolean;
  typeSpeed?: number;
  deleteSpeed?: number;
  delaySpeed?: number;
}

const useTypewriter = (
  words: string[],
  options: TypewriterOptions = {}
): [string, () => void, boolean] => {
  const {
    loop = false,
  } = options;

  const [text, setText] = useState('');
  const [isDeleting, setIsDeleting] = useState(false);
  const [wordIndex, setWordIndex] = useState(0);
  const [charIndex, setCharIndex] = useState(0);
  const [isPaused, setIsPaused] = useState(false);
  const [isDone, setIsDone] = useState(false);

  // To prevent stale closures in setTimeout
  const currentOptionsRef = useRef(options);
  const wordsRef = useRef(words);
  const stateRef = useRef({ isDeleting, wordIndex, charIndex, isPaused, isDone, loop });

  useEffect(() => {
    currentOptionsRef.current = options;
    wordsRef.current = words;
    stateRef.current = { 
        isDeleting, 
        wordIndex, 
        charIndex, 
        isPaused, 
        isDone, 
        loop: options.loop ?? false 
    };
  }, [options, words, isDeleting, wordIndex, charIndex, isPaused, isDone]);


  const handleTyping = useCallback(() => {
    const { isDeleting, wordIndex, isPaused, isDone, loop } = stateRef.current;
    const currentWords = wordsRef.current;
    const { delaySpeed: currentDelaySpeed = 1500 } = currentOptionsRef.current;


    if (isPaused || isDone) return;
    if (!currentWords || currentWords.length === 0) {
        setIsDone(true);
        return;
    }

    const currentWord = currentWords[wordIndex % currentWords.length];
    if (!currentWord) return;

    if (isDeleting) {
      setText(currentWord.substring(0, stateRef.current.charIndex - 1));
      setCharIndex((prev) => prev - 1);

      if (stateRef.current.charIndex - 1 === 0) {
        setIsDeleting(false);
        setWordIndex((prev) => prev + 1); 
        // If it's not looping and we've just incremented past the last word's deletion
        if (!loop && stateRef.current.wordIndex + 1 >= currentWords.length) {
          // This case is tricky if only one word and not looping.
          // isDone should be set when typing of the last word finishes.
        }
      }
    } else { // Typing
      setText(currentWord.substring(0, stateRef.current.charIndex + 1));
      setCharIndex((prev) => prev + 1);

      if (stateRef.current.charIndex + 1 === currentWord.length) { // Finished typing current word
        if (!loop && stateRef.current.wordIndex >= currentWords.length - 1) {
            setIsDone(true);
            return; 
        }
        // If looping, or if not looping but there are more words, prepare for next action
        if (loop || stateRef.current.wordIndex < currentWords.length - 1) {
            setTimeout(() => {
                setIsDeleting(true);
            }, currentDelaySpeed);
        }
      }
    }
  }, []); // Dependencies are managed via refs to avoid frequent re-creation


  useEffect(() => {
    if (stateRef.current.isDone || stateRef.current.isPaused) return;
    
    const { typeSpeed: currentTypeSpeed = 120, deleteSpeed: currentDeleteSpeed = 80 } = currentOptionsRef.current;
    const currentTimeoutSpeed = stateRef.current.isDeleting ? currentDeleteSpeed : currentTypeSpeed;

    const typingTimeout = setTimeout(handleTyping, currentTimeoutSpeed);
    return () => clearTimeout(typingTimeout);
  }, [text, handleTyping]); // Re-run when text changes (which triggers next char) or if handleTyping changes (rare)
  
  const control = useCallback(() => {
     setIsPaused(prev => !prev);
  }, []);

  return [text, control, isDone];
};

export default useTypewriter;
