import React, { useEffect, useRef, useState } from 'react';
import { gsap } from 'gsap';
import { NavLink } from '../constants';

interface NavbarProps {
  navLinks: NavLink[];
  brandName: string;
}

const Navbar: React.FC<NavbarProps> = ({ navLinks, brandName }) => {
  const navRef = useRef<HTMLElement>(null);
  const brandRef = useRef<HTMLAnchorElement>(null);
  const linkRefs = useRef<(HTMLAnchorElement | null)[]>([]);
  const resumeIconRef = useRef<SVGSVGElement>(null); // Ref for the resume icon

  const [comingSoonMessage, setComingSoonMessage] = useState<string | null>(null);
  const [tooltipPosition, setTooltipPosition] = useState<{ top: number; left: number } | null>(null);
  const tooltipRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    linkRefs.current = linkRefs.current.slice(0, navLinks.length);
  }, [navLinks]);

  useEffect(() => {
    const ctx = gsap.context(() => {
      if (navRef.current) {
        gsap.fromTo(navRef.current,
          { y: -80, opacity: 0 },
          { y: 0, opacity: 1, duration: 0.7, ease: 'power3.out', delay: 0.3 }
        );
      }
      // Brand hover
      if (brandRef.current) {
        brandRef.current.addEventListener('mouseenter', () => gsap.to(brandRef.current, { 
            scale: 1.05, 
            textShadow: "0 0 5px var(--current-brand-text-glow-strong)",
            duration: 0.2, 
            ease: 'power1.out' 
        }));
        brandRef.current.addEventListener('mouseleave', () => gsap.to(brandRef.current, { 
            scale: 1, 
            textShadow: "none",
            duration: 0.2, 
            ease: 'power1.out' 
        }));
      }
      // Link hovers
      linkRefs.current.forEach(linkEl => {
        if (linkEl) {
          const isText = linkEl.classList.contains('nav-text-link');
          const isButton = linkEl.classList.contains('nav-button-link');
          const currentResumeIconRef = linkEl.querySelector('svg.resume-icon-svg'); // More specific selector

          const commonButtonHoverIn = () => {
            gsap.to(linkEl, { y: -3, scale: 1.04, duration: 0.25, ease: 'power2.out' });
            if (currentResumeIconRef && linkEl.classList.contains('resume-button')) { // Check if it's the resume button
              gsap.to(currentResumeIconRef, { y: -2, scaleY: 1.1, scaleX: 1.05, duration: 0.15, yoyo: true, repeat: 1, ease: 'power1.inOut' });
            }
          };
          const commonButtonHoverOut = () => {
             gsap.to(linkEl, { y: 0, scale: 1, duration: 0.2, ease: 'power2.out' });
          };

          linkEl.addEventListener('mouseenter', () => {
            if (isText && window.getComputedStyle(linkEl).display !== 'none') { 
              gsap.to(linkEl, { y: -2, backgroundColor: 'rgba(120,120,120,0.15)', color: 'var(--current-nav-link-hover)', duration: 0.2, ease: 'power1.out' });
            } else if (isButton) {
              commonButtonHoverIn();
            }
          });
          linkEl.addEventListener('mouseleave', () => {
            if (isText && window.getComputedStyle(linkEl).display !== 'none') { 
              gsap.to(linkEl, { y: 0, backgroundColor: 'transparent', color: 'var(--current-nav-link-idle)', duration: 0.2, ease: 'power1.out' });
            } else if (isButton) {
              commonButtonHoverOut();
            }
          });
           linkEl.addEventListener('focus', () => { 
            if (isText && window.getComputedStyle(linkEl).display !== 'none') gsap.to(linkEl, { y: -2, backgroundColor: 'rgba(120,120,120,0.15)', color: 'var(--current-nav-link-hover)', duration: 0.2, ease: 'power1.out' });
            if (isButton) commonButtonHoverIn();
          });
          linkEl.addEventListener('blur', () => {
            if (isText && window.getComputedStyle(linkEl).display !== 'none') gsap.to(linkEl, { y: 0, backgroundColor: 'transparent', color: 'var(--current-nav-link-idle)', duration: 0.2, ease: 'power1.out' });
            if (isButton) commonButtonHoverOut();
          });
        }
      });

    }, navRef); 

    return () => ctx.revert();
  }, [navLinks]);

  useEffect(() => {
    let timer: NodeJS.Timeout | undefined;
    if (comingSoonMessage && tooltipRef.current && tooltipPosition) {
      gsap.fromTo(tooltipRef.current, 
        { opacity: 0, scale: 0.8, y: 10 }, 
        { opacity: 1, scale: 1, y: 0, duration: 0.3, ease: 'back.out(1.7)' }
      );
      timer = setTimeout(() => {
        gsap.to(tooltipRef.current, { 
          opacity: 0, scale: 0.8, y: 10, duration: 0.3, ease: 'back.in(1.7)', 
          onComplete: () => {
            setComingSoonMessage(null);
            setTooltipPosition(null);
          }
        });
      }, 2500);
    }
    return () => {
      if (timer) {
        clearTimeout(timer);
      }
    };
  }, [comingSoonMessage, tooltipPosition]);

  const handleLinkClick = (event: React.MouseEvent<HTMLAnchorElement>, link: NavLink) => {
    if (link.isComingSoon) {
      event.preventDefault();
      const rect = event.currentTarget.getBoundingClientRect();
      setTooltipPosition({ 
        top: rect.bottom + window.scrollY + 8, 
        left: rect.left + window.scrollX + rect.width / 2 
      });
      setComingSoonMessage("Coming soon! My thoughts and blogs will be here.");
      return; 
    }

    if (link.href.startsWith('#') && link.href.length > 1) {
      event.preventDefault();
      const targetId = link.href.substring(1);
      const targetElement = document.getElementById(targetId);
      if (targetElement) {
        targetElement.scrollIntoView({ behavior: 'smooth', block: 'start' });
      }
    }
    // For external links or file links (like resume.pdf with target="_blank"),
    // the default browser behavior will apply.
  };

  return (
    <>
      <div className="fixed top-5 left-0 right-0 z-50 flex justify-center pointer-events-none">
        <nav 
          ref={navRef} 
          className="bg-[var(--current-nav-bg)] shadow-nav rounded-full 
                     flex items-center justify-between
                     px-3 sm:px-4 md:px-6 lg:px-8 xl:px-10
                     py-2 sm:py-2 md:py-2.5 lg:py-3 xl:py-3.5
                     pointer-events-auto
                     backdrop-blur-md 
                     transition-colors duration-[var(--global-transition-duration)] ease-in-out" 
        >
          <a 
            ref={brandRef}
            href="#" 
            onClick={(e) => {
              e.preventDefault();
              window.scrollTo({ top: 0, behavior: 'smooth' });
            }}
            className="text-base sm:text-lg md:text-xl lg:text-2xl xl:text-3xl font-sans font-bold text-[var(--current-brand-name-color)] whitespace-nowrap transition-opacity"
            aria-label="Homepage, scroll to top"
          >
            {brandName}
          </a>
          <div className="flex items-center space-x-1.5 sm:space-x-2 md:space-x-2.5 lg:space-x-3 xl:space-x-4 ml-2 sm:ml-3 md:ml-4 lg:ml-6 xl:ml-8">
            {navLinks.map((link, index) => (
              <a
                key={link.label}
                ref={el => { linkRefs.current[index] = el; }}
                href={link.href}
                target={link.isPrimary ? '_blank' : (link.href.startsWith('http') ? '_blank' : '_self')}
                rel={link.isPrimary || link.href.startsWith('http') ? 'noopener noreferrer' : ''}
                onClick={(e) => handleLinkClick(e, link)}
                className={`
                  font-sans font-medium transition-all duration-200 ease-in-out whitespace-nowrap
                  focus:outline-none focus-visible:ring-2 focus-visible:ring-[var(--current-brand-text)] focus-visible:ring-opacity-50
                  text-xs sm:text-sm md:text-base lg:text-lg xl:text-xl
                  ${link.isTextLink
                    ? 'hidden md:inline-block text-[var(--current-nav-link-idle)] hover:text-[var(--current-nav-link-hover)] rounded-full px-2 sm:px-2.5 md:px-3 lg:px-3.5 xl:px-4 py-1 sm:py-1.5 md:py-2 lg:py-2.5 xl:py-3 nav-text-link'
                    : link.isContact
                    ? 'bg-[var(--current-button-contact-bg)] text-[var(--current-button-contact-text)] hover:opacity-90 rounded-full shadow-sm px-2.5 sm:px-3 md:px-4 lg:px-5 xl:px-6 py-1 sm:py-1.5 md:py-2 lg:py-2.5 xl:py-3 nav-button-link interactive-button'
                    : link.isPrimary // Resume button
                    ? 'bg-[var(--current-button-resume-bg)] text-[var(--current-button-resume-text)] hover:opacity-90 rounded-full shadow-sm flex items-center gap-x-1 sm:gap-x-1.5 md:gap-x-2 lg:gap-x-2.5 xl:gap-x-3 px-2.5 sm:px-3 md:px-4 lg:px-5 xl:px-6 py-1 sm:py-1.5 md:py-2 lg:py-2.5 xl:py-3 nav-button-link resume-button interactive-button'
                    : 'text-[var(--current-nav-link-idle)] hover:text-[var(--current-nav-link-hover)]' 
                  }
                `}
                aria-label={link.label + (link.isComingSoon ? " (Coming Soon)" : "")}
              >
                {link.label}
                {link.isPrimary && (
                  <svg 
                    xmlns="http://www.w3.org/2000/svg" 
                    viewBox="0 0 20 20" 
                    fill="currentColor" 
                    className="w-3 h-3 sm:w-3.5 sm:h-3.5 md:w-4 md:h-4 lg:w-4.5 lg:h-4.5 xl:w-5 xl:h-5 opacity-90 resume-icon-svg" // Added class
                    style={{willChange: 'transform'}} // Hint for GSAP
                    ref={resumeIconRef}
                  >
                    <path d="M10.75 2.75a.75.75 0 00-1.5 0v8.614L6.295 8.235a.75.75 0 10-1.09 1.03l4.25 4.5a.75.75 0 001.09 0l4.25-4.5a.75.75 0 00-1.09-1.03l-2.955 3.129V2.75z" />
                    <path d="M3.5 12.75a.75.75 0 00-1.5 0v2.5A2.75 2.75 0 004.75 18h10.5A2.75 2.75 0 0018 15.25v-2.5a.75.75 0 00-1.5 0v2.5c0 .69-.56 1.25-1.25 1.25H4.75c-.69 0-1.25-.56-1.25-1.25v-2.5z" />
                  </svg>
                )}
              </a>
            ))}
          </div>
        </nav>
      </div>
      {comingSoonMessage && tooltipPosition && (
        <div
          ref={tooltipRef}
          role="status"
          aria-live="polite"
          className="fixed bg-nav-bg text-nav-link-hover text-xs font-sans p-2.5 rounded-md shadow-lg z-[60] pointer-events-none"
          style={{ 
            top: tooltipPosition.top, 
            left: tooltipPosition.left, 
            transform: 'translateX(-50%)', 
            opacity: 0 
          }}
        >
          {comingSoonMessage}
        </div>
      )}
    </>
  );
};

export default Navbar;