import React, { forwardRef, useEffect, useRef, useCallback } from 'react';
import { gsap } from 'gsap';
import { LabNote } from '../constants';

interface LabNotesSectionProps {
  notes: LabNote[];
}

const MOUSE_INTERACTION_RADIUS = 200; 
const CARD_PUSH_STRENGTH = 35;      
const CARD_MOVE_DURATION = 0.4;     

interface QuickToData {
  x: (value: number) => void;
  y: (value: number) => void;
  target: HTMLDivElement | null;
}

const LabNotesSection = forwardRef<HTMLDivElement, LabNotesSectionProps>(({ notes }, ref) => {
  const sectionRef = (ref as React.RefObject<HTMLDivElement>) || useRef<HTMLDivElement>(null);
  const noteRefs = useRef<(HTMLDivElement | null)[]>([]);
  const quickToRefs = useRef<Record<string, QuickToData>>({});
  
  const animationFrameId = useRef<number | null>(null);
  // Initialize mousePosition to avoid null issues, e.g., center of screen or 0,0
  const mousePosition = useRef<{ x: number; y: number }>({ x: window.innerWidth / 2, y: window.innerHeight / 2 });
  const isMouseInSection = useRef(false);

  useEffect(() => {
    noteRefs.current = noteRefs.current.slice(0, notes.length);
    noteRefs.current.forEach((card, index) => {
      if (card && notes[index]) { // Ensure card and note data exist
        const noteId = notes[index].id;
        // Only create if not already existing, or if the target card instance has changed
        if (!quickToRefs.current[noteId] || quickToRefs.current[noteId]?.target !== card) {
          quickToRefs.current[noteId] = {
            x: gsap.quickTo(card, "x", { duration: CARD_MOVE_DURATION, ease: "power2.out" }),
            y: gsap.quickTo(card, "y", { duration: CARD_MOVE_DURATION, ease: "power2.out" }),
            target: card, // Store the card as the target
          };
        }
      }
    });
  }, [notes]); // Re-run if notes prop changes.

  const updateCardPositions = useCallback(() => {
    if (!sectionRef.current) { // Should ideally not happen if loop is controlled properly
      if (animationFrameId.current) cancelAnimationFrame(animationFrameId.current);
      animationFrameId.current = null;
      return;
    }

    let isAnyCardActive = false; // To determine if loop should continue

    noteRefs.current.forEach((card, index) => {
      if (!card || !notes[index]) return;

      const noteId = notes[index].id;
      const qToData = quickToRefs.current[noteId];
      if (!qToData) return;

      let targetX = 0;
      let targetY = 0;

      if (isMouseInSection.current) { 
        const cardRect = card.getBoundingClientRect();
        // Ensure cardRect has valid dimensions, otherwise skip (e.g. if display:none)
        if (cardRect.width === 0 && cardRect.height === 0) return;

        const cardCenterX = cardRect.left + cardRect.width / 2;
        const cardCenterY = cardRect.top + cardRect.height / 2;

        const dx = cardCenterX - mousePosition.current.x;
        const dy = cardCenterY - mousePosition.current.y;
        const distance = Math.sqrt(dx * dx + dy * dy);

        if (distance < MOUSE_INTERACTION_RADIUS) {
          const angle = Math.atan2(dy, dx);
          const pushFactor = (1 - distance / MOUSE_INTERACTION_RADIUS);
          targetX = Math.cos(angle) * CARD_PUSH_STRENGTH * pushFactor;
          targetY = Math.sin(angle) * CARD_PUSH_STRENGTH * pushFactor;
        }
      }
      
      qToData.x(targetX);
      qToData.y(targetY);

      // Check if this card is still considered "active" (not at its resting 0,0)
      // GSAP's _gsap object can give current transform values, but direct check is simpler
      // We assume if targetX/Y are non-negligible, it's active or becoming active
      if (Math.abs(targetX) > 0.01 || Math.abs(targetY) > 0.01) {
        isAnyCardActive = true;
      } else {
        // If target is 0,0, check current GSAP property if needed for more precise "at rest"
        // For now, if target is 0,0, we assume it's heading to rest.
        // A more robust check for "at rest" would query gsap.getProperty(card, "x").
         const currentX = gsap.getProperty(card, "x") as number;
         const currentY = gsap.getProperty(card, "y") as number;
         if (Math.abs(currentX) > 0.01 || Math.abs(currentY) > 0.01) {
            isAnyCardActive = true;
         }
      }
    });

    if (isMouseInSection.current || isAnyCardActive) {
      animationFrameId.current = requestAnimationFrame(updateCardPositions);
    } else {
      animationFrameId.current = null; // Stop the loop
    }
  }, [notes, sectionRef]); // Dependencies: notes (props), sectionRef (stable after mount)

  const handleMouseMove = useCallback((event: MouseEvent) => {
    mousePosition.current = { x: event.clientX, y: event.clientY };

    if (!sectionRef.current) {
      if (isMouseInSection.current) isMouseInSection.current = false;
      return;
    }

    const rect = sectionRef.current.getBoundingClientRect();
    const currentlyOver = 
      event.clientX >= rect.left - MOUSE_INTERACTION_RADIUS &&
      event.clientX <= rect.right + MOUSE_INTERACTION_RADIUS &&
      event.clientY >= rect.top - MOUSE_INTERACTION_RADIUS &&
      event.clientY <= rect.bottom + MOUSE_INTERACTION_RADIUS;

    if (currentlyOver) {
      if (!isMouseInSection.current) { 
        isMouseInSection.current = true;
      }
      if (!animationFrameId.current) { // Start loop if not already running
        animationFrameId.current = requestAnimationFrame(updateCardPositions);
      }
    } else { 
      if (isMouseInSection.current) { 
        isMouseInSection.current = false;
        // The animation loop (`updateCardPositions`) will see `isMouseInSection.current == false`,
        // return cards to origin, and then stop itself when cards are at rest.
      }
    }
  }, [sectionRef, updateCardPositions]);

  useEffect(() => {
    const mainSection = sectionRef.current; // Capture for cleanup
    if (!mainSection) return;

    const ctx = gsap.context(() => {
      if (noteRefs.current.length > 0) {
        noteRefs.current.forEach((card, index) => {
          if (card) {
            // Skip initial animation for cards hidden on mobile if screen is mobile
            // GSAP ScrollTrigger won't run if the element is display:none, so this is okay
            gsap.fromTo(card,
              { opacity: 0, scale: 0.9, y: 30 },
              {
                opacity: 1,
                scale: 1,
                y: 0,
                duration: 0.5,
                ease: 'power2.out',
                scrollTrigger: {
                  trigger: card,
                  start: 'top 90%', 
                  toggleActions: 'play none none none',
                },
                delay: (window.innerWidth < 640 ? index * 0.1 : (index % 2) * 0.15),
              }
            );
          }
        });
      }
    }, mainSection); 

    window.addEventListener('mousemove', handleMouseMove);
    
    // Initial check in case mouse is already over section on load
    // This requires sectionRef.current to be available.
    if (mainSection) {
        const rect = mainSection.getBoundingClientRect();
        if (mousePosition.current.x >= rect.left - MOUSE_INTERACTION_RADIUS &&
            mousePosition.current.x <= rect.right + MOUSE_INTERACTION_RADIUS &&
            mousePosition.current.y >= rect.top - MOUSE_INTERACTION_RADIUS &&
            mousePosition.current.y <= rect.bottom + MOUSE_INTERACTION_RADIUS) {
            if (!isMouseInSection.current) isMouseInSection.current = true;
            if (!animationFrameId.current) {
                animationFrameId.current = requestAnimationFrame(updateCardPositions);
            }
        }
    }


    return () => {
      ctx.revert();
      window.removeEventListener('mousemove', handleMouseMove);
      if (animationFrameId.current) {
        cancelAnimationFrame(animationFrameId.current);
        animationFrameId.current = null;
      }
    };
  }, [handleMouseMove, sectionRef, updateCardPositions]);


  return (
    <section 
      id="lab-notes" 
      ref={sectionRef} 
      className="w-full pt-0 pb-12 md:pb-16 lg:pb-20 xl:pb-24 opacity-0" 
    >
      <div className="container mx-auto px-6 max-w-4xl xl:max-w-5xl">
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-x-2 gap-y-4 md:gap-x-3 md:gap-y-5">
          {notes.map((note, index) => (
            <div
              key={note.id}
              ref={el => { noteRefs.current[index] = el; }}
              className={`
                bg-nav-bg bg-opacity-70 p-6 md:p-8 rounded-lg 
                border border-[var(--current-brand-text)] border-opacity-30 
                hover:border-opacity-70
                shadow-lg hover:shadow-xl 
                transition-all duration-300 ease-in-out group 
                ${note.rotationClass} ${note.hoverRotationClass}
                relative
                ${note.hideOnMobile ? 'hidden sm:block' : ''}
              `} 
            >
              <p className="font-mono text-base md:text-lg text-gray-100 leading-relaxed text-center">
                {note.text}
              </p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
});

LabNotesSection.displayName = 'LabNotesSection';
export default LabNotesSection;
