import React from 'react';

interface CloudProps {
  className?: string;
  animationClass?: string;
}

const Cloud: React.FC<CloudProps> = ({ className = '', animationClass = '' }) => {
  return (
    <div
      className={`absolute pointer-events-none cloud-base ${className} ${animationClass}`}
      style={{
        // The filter is applied here to the whole cloud including its puffs
        filter: 'var(--current-cloud-filter)',
        transition: 'filter 0.7s ease-in-out, background-color 0.7s ease-in-out',
        // The main element itself forms a part of the cloud
        backgroundColor: 'var(--current-cloud-color)',
        // Asymmetrical blob shape for the main cloud body
        borderRadius: '50% 60% 70% 50% / 50% 50% 60% 60%', 
      }}
      role="img"
      aria-label="Decorative cloud"
    >
      {/* CSS pseudo-elements ::before and ::after will be styled in index.html 
          to add more puffs to this base, creating a more voluminous cloud. 
          They will inherit the filter and use the same background color. */}
    </div>
  );
};

export default Cloud;