import React, { forwardRef, useRef, useState, useEffect } from 'react';
import { gsap } from 'gsap';
import { MAIN_CONTENT } from '../constants';

interface FloatingWorkButtonProps {}

const FloatingWorkButton = forwardRef<HTMLDivElement, FloatingWorkButtonProps>((_props, ref) => {
  const buttonContainerRef = (ref as React.RefObject<HTMLDivElement>) || useRef<HTMLDivElement>(null);
  const buttonRef = useRef<HTMLAnchorElement>(null);
  const [buttonText, setButtonText] = useState(MAIN_CONTENT.workButtonText);

  const easterEggTimeoutRef = useRef<number | null>(null);
  const revertTextTimeoutRef = useRef<number | null>(null);

  useEffect(() => {
    // GSAP Hover effect for the container was removed as per request.
    // Original code:
    // if (buttonContainerRef.current) {
    //   const container = buttonContainerRef.current;
    //   container.addEventListener('mouseenter', () => {
    //     gsap.to(container, { scale: 1.08, boxShadow: '0 10px 20px rgba(0,0,0,0.25)', duration: 0.25, ease: 'power2.out' });
    //   });
    //   container.addEventListener('mouseleave', () => {
    //     gsap.to(container, { scale: 1, boxShadow: '0 6px 12px rgba(0,0,0,0.15)', duration: 0.2, ease: 'power2.out' });
    //   });
    // }
  }, [buttonContainerRef]);


  useEffect(() => {
    const scheduleEasterEgg = () => {
      if (easterEggTimeoutRef.current) {
        clearTimeout(easterEggTimeoutRef.current);
      }
      const randomDelay = Math.random() * (18000 - 6000) + 6000; // 6-18 seconds

      easterEggTimeoutRef.current = window.setTimeout(() => {
        if (buttonRef.current) {
          gsap.timeline()
            .to(buttonRef.current, { opacity: 0, y: -5, duration: 0.2, ease: 'power1.in' })
            .add(() => setButtonText(MAIN_CONTENT.workButtonEasterEggText))
            .set(buttonRef.current, { y: 5 }) // Position for entry
            .to(buttonRef.current, { opacity: 1, y: 0, duration: 0.25, ease: 'power1.out' });
        } else {
            setButtonText(MAIN_CONTENT.workButtonEasterEggText); // Fallback if ref not ready
        }
        
        if (revertTextTimeoutRef.current) clearTimeout(revertTextTimeoutRef.current);
        revertTextTimeoutRef.current = window.setTimeout(() => {
          if (buttonRef.current) {
            gsap.timeline()
              .to(buttonRef.current, { opacity: 0, y: -5, duration: 0.2, ease: 'power1.in' })
              .add(() => setButtonText(MAIN_CONTENT.workButtonText))
              .set(buttonRef.current, { y: 5 })
              .to(buttonRef.current, { opacity: 1, y: 0, duration: 0.25, ease: 'power1.out', onComplete: scheduleEasterEgg });
          } else {
            setButtonText(MAIN_CONTENT.workButtonText); // Fallback
            scheduleEasterEgg();
          }
        }, 800); // Easter egg visible for 0.8 seconds
      }, randomDelay);
    };

    scheduleEasterEgg(); 

    return () => {
      if (easterEggTimeoutRef.current) clearTimeout(easterEggTimeoutRef.current);
      if (revertTextTimeoutRef.current) clearTimeout(revertTextTimeoutRef.current);
      if (buttonContainerRef.current) {
        gsap.killTweensOf(buttonContainerRef.current); 
      }
    };
  }, [buttonContainerRef]);

  const handleClick = (_e: React.MouseEvent<HTMLAnchorElement>) => {
    if (buttonRef.current) {
      gsap.killTweensOf(buttonRef.current); 
      gsap.timeline()
        .to(buttonRef.current, { 
            scale: 0.95, 
            filter: 'brightness(0.9)', 
            duration: 0.08, 
            ease: 'power2.in' 
        })
        .to(buttonRef.current, { 
            scale: 1, 
            filter: 'brightness(1)',
            duration: 0.25, 
            ease: 'elastic.out(1, 0.6)' 
        });
    }
  };
  
  return (
    <div
      ref={buttonContainerRef} 
      className="fixed bottom-6 left-6 z-40 
                 p-1 rounded-xl shadow-lg 
                 bg-[var(--current-work-button-bg)] bg-opacity-30 backdrop-blur-sm
                 transition-all duration-300 ease-in-out group" 
      role="complementary"
      aria-label="Work with me call to action"
      style={{ willChange: 'transform, box-shadow' }} 
    >
      <a
        ref={buttonRef}
        href={`mailto:${MAIN_CONTENT.email}`}
        className="font-sans font-semibold py-2.5 px-5 sm:py-3 sm:px-6 xl:py-3.5 xl:px-7 rounded-lg 
                   bg-[var(--current-work-button-bg)] text-[var(--current-work-button-text)]
                   transition-colors duration-200 ease-in-out
                   text-sm sm:text-base xl:text-lg tracking-wider block text-center
                   focus:outline-none focus-visible:ring-2 focus-visible:ring-[var(--current-brand-text)]
                   interactive-button"
        aria-label={`Contact me to work together. Current button text: ${buttonText}`}
        onClick={handleClick}
        onKeyDown={(e) => { if (e.key === 'Enter' || e.key === ' ') { e.preventDefault(); handleClick(e as any); } }}
        style={{ willChange: 'opacity, transform, background-color, color' }} 
      >
        {buttonText}
      </a>
    </div>
  );
});

FloatingWorkButton.displayName = 'FloatingWorkButton';
export default FloatingWorkButton;
